export default `
<script>
{
    let ages = [12, 13, 16, 20, 25, 22]
    write(ages.find((age, index, arr) => age>=18))
}
{
    let ages = [12, 13, 16, 20, 25, 22]
    write(ages.findIndex((age, index, arr) => age>=18))
}
</script>
`



